<template>
  <div class="module-block">

    <modal-window v-model:visible="showDetails" modalWidth="26em">
      <template #content>
        <div class="module-name" style="text-align:center">{{ module.name }}</div>
        <div class="modal-text">{{ module.description }}</div>
        <div v-if="module.blockNames.length">
          <br/>
          <p style="text-align:center"><b>Теми, що входять до курсу:</b></p>
          <ul style="color:gray;"><li v-for="name in module.blockNames" :key="name">{{ name }}</li></ul>
        </div>
      </template>
      <template #buttons v-if="module.group == 'Мої курси'">
        <button class="button-small" :style="{ background: module.color }" @click="gotoModule()">
          Перейти&nbsp;<span class="material-icons">arrow_forward</span>
        </button>
      </template>
    </modal-window>
    
    <div :style='{
     background: `linear-gradient(#0004, #0004), url(${backgroundURL})`,
     backgroundSize: "cover", backgroundPosition: `left 0 top 10%`}'>
      <div class="module-header">
        <div class="module-subject line-l">МАТЕМАТИКА</div>
        <div class="module-course">{{ module.course }}</div>
      </div>
    </div>

    <div class="bar-wrapper">
      <bar-horizontal :data='[module.tasksCompleted, (module.tasksTotal - module.tasksCompleted) || 100]' :colors='[module.color, "#0002"]'/>
    </div>

    <div class="module-body">
      <div class="module-name">{{ module.name }}</div>
      <div class="module-description" v-if="module.description">
        {{ module.description.slice(0, 115) + "..." }}&ensp;
        <span class="details" @click="showDetails = true">Детальніше</span>
      </div>
    </div>

    <div class="module-info">
      <span v-if='isInteractable'>
        <span v-if="module.type == 'prefabs'"><i class="material-icons-outlined">factory</i>&ensp;Генератор тестів</span>
        <span v-else>{{ module.blocksTotal }} тем&nbsp;&thinsp;&bull;&thinsp;&nbsp;{{ module.tasksTotal }} завдань</span>
      </span>
      <button v-if='isInteractable' class="button-small" :style="{ background: module.color }" @click="gotoModule()">
        Перейти&nbsp;<span class="material-icons">arrow_forward</span>
      </button>
      <!-- <button v-else class="button-small" :style="{ background: module.color }" @click="showDetails = true">
        Детально&nbsp;<span class="material-icons">details</span>
      </button> -->
    </div>

  </div>
</template>

<script>
import modalWindow from '@/components/modal-basic.vue'
import barHorizontal from '@/components/bar-horizontal.vue'
export default {
  components: { modalWindow, barHorizontal },
  props: { 
    module: Object 
  },
  data() { return {
    showDetails: false,
    isInteractable: this.module.group == 'Мої курси' || this.module.group == 'Доступні курси'
  }},
  computed: {
    backgroundURL() { return `${this.$cloudinary}previews/${this.module._id}-small.webp` },
  },
  methods: {
    gotoModule() {
      localStorage.setItem('colorTheme', this.module.color)
      localStorage.setItem('moduleName', this.module.name)
      localStorage.setItem('moduleId', this.module._id)
      if (this.module.type == 'prefabs') this.$router.push('prefabs')
      else this.$router.push('lessons')
    }
  }
}
</script>

<style lang="scss" scoped>
//* {border: 1px solid red}
li {
  margin-bottom: .5em;
}
.module-block {
  display: flex;
  flex-direction: column;
  max-width: 32em;
  margin: 1em auto;
  background: #fff;
  border-radius: 4px;
  box-shadow: 0 2px 8px rgba(0,0,0,.12);
}
.module-header {
  display: flex;
  flex-direction: column;
  padding: 1em 2em;
  .module-subject {
    margin-top: 10vmin;
    margin-bottom: 1vmin;
    font-weight: bold;
    font-size: .9em;
    color: #dbdbdb;
  }
  .module-course {
    font-size: 2.5em;
    font-weight: bold;
    font-family: 'Oswald';
    text-transform: uppercase;
    letter-spacing: 2px;
    color: #fff;
  }
}
.module-body {
  margin-bottom: auto;
  padding: 2em 1.75em 1.5em 1.75em;
  .details { 
    text-decoration: underline;
    &:hover { opacity: .5 }
  }
}

.module-name {
  font-size: 1.5em;
  font-weight: bold;
}

.module-description {
  margin-top: 1em;
  color: #7a7a7a;
  line-height: 1.5em;
  font-size: .95em;
}

.module-info {
  padding: 2vmin 1.75em;
  display: flex;
  align-items: center;
  color: gray;
  font-size: .95em;
  background: #0001;
  button { 
    margin-left: auto;
    color: #fff;
    white-space: nowrap;
  }
}

.bar-wrapper { height: .25em; }

</style>