<template>
<main-layout :backgroundImage="`${$cloudinary}main.png`" backgroundOffset="50">

  <template #header>
    <personal-info-header :userinfo='`${tasksCompleted} / ${tasksTotal} завдань`'/>
  </template>

  <template #body>
    <div v-for="moduleGroup in moduleGroups" :key="moduleGroup._id">
      <div class="section-name">{{ moduleGroup._id }}</div>
      <div class="modules-section">
        <module-card v-for="module in moduleGroup.modules" :key="module.name" :module="module"/>
        <div/><div/><div/>
      </div>
    </div>
  </template>

</main-layout>
</template>

<script>
import mainLayout from '@/layouts/main-layout.vue'
import moduleCard from '@/components/module-card.vue'
import personalInfoHeader from '@/components/personal-info-header.vue'
export default {
  components: { mainLayout, moduleCard, personalInfoHeader },
  computed: {
    moduleGroups() { return this.$store.state.modules },
    tasksCompleted() { return this.moduleGroups.map(e => e.modules).flat().map(e => e.tasksCompleted).sum() },
    tasksTotal() { return this.moduleGroups.map(e => e.modules).flat().map(e => e.tasksTotal).sum() },
  },
  mounted() {

  }
}
</script>

<style lang="scss" scoped>
