@import '../styles.scss';
.section-name {
  font-size: 1.15em;
  font-weight: bold;
  padding: 1em 1.5em;
  margin-bottom: .5em;
  border-radius: 4px;
  text-transform: uppercase;
  background: rgba(0,0,0,.025);
  @media (max-width: 720px) {
    margin: auto;
    margin-bottom: .5em;
    //max-width: 400px;
  }
}
.modules-section {
  display: flex;
  flex-wrap: wrap;
  column-gap: 2em;
  margin-bottom: 4em;
  & > * { flex: 1 1 20em }
  @media (min-width: 480px) and (max-width: 720px) { 
    //margin: 0 5vw; 
    margin-bottom: 2.5em;
  }
}

</style>