<template>
  <div class="relative elastic">
    <svg viewBox='0 0 100 10' preserveAspectRatio="none">
      <rect v-for='i in data.length' :key='i'
        :width='(percentages[i-1])' height=10
        :x='partials[i-1]'
        :fill='colors[i-1]'/>
    </svg>
  </div>
  <div class="bar-label">
    <slot></slot>
  </div>
</template>

<script>
export default {
  props: {
    data: Array,
    colors: Array,
    labels: Array,
  },
  computed: {
    percentages() { return this.data.map(e => Math.ceil(e / this.data.sum() * 100)) },
    partials() { return [0, ...this.percentages.partialSums()] }
  }
}
</script>

<style lang="scss" scoped>
//* {border: 1px solid red}
div {margin: 0; padding: 0;}
svg {
  position: absolute;
  width: 100%;
  height: 100%;
}
.bar-label{
  position: absolute;
  right: 0;
  top: -1.75em;
  color: gray;
}
.label-text {
  line-height: 1.35em;
  width: 1.35em;
  height: 1.35em;
  border-radius: 50%;
  text-align: center;
  color: white;
  font-weight: bold;
}
.label-mark {
  width: 1.25em;
  height: 1.25em;
  border-radius: 50%;
}
</style>